import React, { Component } from "react";
import SweetAlert from "sweetalert2-react";
import "react-datepicker/dist/react-datepicker.css";
import { TabContent, TabPane } from "reactstrap";
import classnames from "classnames";
import { listofAgentsecondmore } from "../../../OxyJs/Afterlogin";
import Spinner from "reactstrap/lib/Spinner";

export default class Departments extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeTab: 1,
      isCardRemove: false,
      isCollapsed: false,
      show: false,
      getData: [],
      isloading: false,
    };
  }

  componentDidMount() {
    listofAgentsecondmore("loading")
      .then((response) => {
        console.log(response);
        if (response === undefined) {
        } else {
          this.setState({
            getData: response,
            isloading: true,
          });
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }
  render() {
    const { activeTab } = this.state;
    const { history } = this.props;
    return (
      <>
        <div className="section-body">
          <div className="container-fluid">
            <div className="d-flex justify-content-between align-items-center">
              <div className="header-action">
                <h1 className="page-title"> Registered Agents</h1>
              </div>
            </div>
          </div>
        </div>
        <div className="section-body mt-4">
          <div className="container-fluid">
            <TabContent activeTab={activeTab}>
              <TabPane tabId={1} className={classnames(["fade show"])}>
                <div className="table-responsive">
                  {this.state.getData.length === 0 ? (
                    <Spinner
                      color="primary"
                      className="m-5 text-center"
                      style={{
                        position: "absolute",
                        left: "40%",
                        top: "60px",
                      }}
                    ></Spinner>
                  ) : (
                    <div className="table-responsive card">
                      <table className="table table-hover table-striped table-vcenter text-nowrap mb-0">
                        <thead>
                          <tr>
                            <th>So</th>
                            <th>Agent</th>
                            <th>Tied Up universities </th>
                          </tr>
                        </thead>
                        <tbody>
                          {this.state.getData.map((data) => {
                            return (
                              <tr
                                key={Math.random()}
                                onClick={() => {
                                  history.push(
                                    `/staff?agentName=${data.agentName}`
                                  );
                                }}
                              >
                                <td>{data.sno}</td>
                                <td>{data.agentName}</td>
                                <td>{data.countOfUniversitiesMappedToAgent}</td>
                              </tr>
                            );
                          })}
                        </tbody>
                      </table>
                    </div>
                  )}
                  <SweetAlert
                    show={this.state.show}
                    title="Are you sure?"
                    text="You will not be able to recover this imaginary file!"
                    type="warning"
                    showCancelButton={true}
                    confirmButtonColor="#dc3545"
                    confirmButtonText="Yes, delete it!"
                    closeOnConfirm={false}
                    onConfirm={() => this.setState({ show: false })}
                  />
                </div>
              </TabPane>
            </TabContent>
          </div>
        </div>
      </>
    );
  }
}
